-color-step-150: #262626;\n//     --ion-color-step-200: #333333;\n//     --ion-color-step-250: #404040;\n//     --ion-color-step-300: #4d4d4d;\n//     --ion-color-step-350: #595959;\n//     --ion-color-step-400: #666666;\n//     --ion-color-step-450: #737373;\n//     --ion-color-step-500: #808080;\n//     --ion-color-step-550: #8c8c8c;\n//     --ion-color-step-600: #999999;\n//     --ion-color-step-650: #a6a6a6;\n//     --ion-color-step-700: #b3b3b3;\n//     --ion-color-step-750: #bfbfbf;\n//     --ion-color-step-800: #cccccc;\n//     --ion-color-step-850: #d9d9d9;\n//     --ion-color-step-900: #e6e6e6;\n//     --ion-color-step-950: #f2f2f2;\n//     --ion-toolbar-background: #ffffff;\n//     --ion-item-background: #ffffff;\n//     --ion-card-background: #ffffff;\n//   }\n//   .md body {\n//     --ion-background-color: #121212;\n//     --ion-background-color-rgb: 18,18,18;\n//     --ion-text-color: #ffffff;\n//     --ion-text-color-rgb: 255,255,255;\n//     --ion-border-color: #222222;\n//     --ion-color-step-50: #1e1e1e;\n//     --ion-color-step-100: #2a2a2a;\n//     --ion-color-step-150: #363636;\n//     --ion-color-step-200: #414141;\n//     --ion-color-step-250: #4d4d4d;\n//     --ion-color-step-300: #595959;\n//     --ion-color-step-350: #656565;\n//     --ion-color-step-400: #717171;\n//     --ion-color-step-450: #7d7d7d;\n//     --ion-color-step-500: #898989;\n//     --ion-color-step-550: #949494;\n//     --ion-color-step-600: #a0a0a0;\n//     --ion-color-step-650: #acacac;\n//     --ion-color-step-700: #b8b8b8;\n//     --ion-color-step-750: #c4c4c4;\n//     --ion-color-step-800: #d0d0d0;\n//     --ion-color-step-850: #dbdbdb;\n//     --ion-color-step-900: #e7e7e7;\n//     --ion-color-step-950: #f3f3f3;\n//     --ion-item-background: #1a1b1e;\n//     --ion-toolbar-background: #1f1f1f;\n//     --ion-tab-bar-background: #1f1f1f;\n//     --ion-card-background: #1e1e1e;\n//   }\n// }\nvideo::-webkit-media-controls-fullscreen-button{display:none;}\nion-checkbox{--border-radius:0px;}\nion-back-button{color:#fff;}\n.popoverClass{\n  .popover-content{border-radius:10px;}\n}\n.editDeleteClass{\n.popover-content {\n  // top: 338px ;\n    left: 160px !important;\n    width: 150px;\n}\n}\n// use this class for manage div in center align form top or bottom start\n.transformDiv{position:relative;top:50%;transform:translate(0%, -50%);}\n// use this class for manage div in center align form top or bottom end\n\n\n.tncData{\n  .redText{color:var(--headerColor);font-size:14px;}\n}\n\n// Like & Comment Css Start\n// Like & Comment Css Start\n.likeModuleDiv{border-top:1px solid gainsboro;border-bottom:0px solid gainsboro;padding:5px 0 0 0;\n  .likeDiv, .commentDiv{\n    ion-icon{font-size:20px;}\n    p{margin:0px;font-size:12px;}\n  }\n}\n.fullCmntList{margin:0;display:inline-block;width:100%;\n  .borderList{border-bottom:0px solid gainsboro;padding:5px 0;/*background:#e5e5e5;*/margin:5px 0px;\n    .commentListData{background: #cdbee5;border-radius:5px;\n      .userText{font-size:14px;font-weight:bold;}\n      .locationText{font-size:12px;margin:5px 0 0 0;color: #3a3737;}\n      .timeText{font-size:10px;color: #3a3737;}\n    }\n  }\n  .borderList:nth-last-child(1){border:0px solid black;}\n}\nion-textarea{--padding-top: 10px !important;--padding-end:10px !important;--padding-start:10px !important;\n  --padding-bottom:10px !important;\n}\nion-input,ion-datetime{--padding-top:5px !important;--padding-end:10px !important;--padding-start:10px !important;\n  --padding-bottom:5px !important;\n}\n.colorChangeLike{color: var(--headerColor);}\n.iosRegisterDiv{\n  ion-input,ion-datetime{--padding-top:10px !important;--padding-end:10px !important;--padding-start:10px !important;\n    --padding-bottom:10px !important;\n  }\n}\n.iconCSSimg{height: 20px !important;width: 20px !important;object-fit: contain !important;margin:-1px 0px -3px 4px;} \n.commentAreaDiv{border: 1px solid gray;border-radius: 10px; \n  .commentDiv{\n    ion-textarea{margin:0;padding:5px 0px;color:#000 !important;--padding:0px !important;\n      --padding-start:0px !important;--padding-top:7px !important;--padding-bottom:7px !important;--padding-end:0 !important;\n      padding: 5px;font-size: 13px; }\n  }\n\n  .sendCommentButton{--background:var(--headerColor);--border-radius:50%;width:30px;height:30px;color:#fff;padding:0;\n    --background-activated: var(--benepikBtnColor);--background-focused: var(--benepikBtnColor);\n    --background-hover: var(--benepikBtnColor);\n    ion-icon{color:#fff;font-size:20px;margin:1px 0 0 0;position:absolute;}\n  }\n  // ion-icon{color:var(--headerColor);font-size:30px;}\n}\n.limitedLikeListDiv{border-bottom:0px solid gainsboro;padding:0 0 10px 0;display:inline-block;width:100%;\n  .likeTitleDiv{font-size:17px;color:#000;font-weight:bold;margin:0;border-bottom:0px dotted #cdcdcd;margin:0 0 10px 0;padding:0 0 5px 0;}\n  .staticWidth{width:20%;margin:auto;float:left;text-align:center;  \n    .likeUserDiv{width:50px;height:50px;position:relative;margin:auto;\n      .imgIcon{width:100%;height:100%;overflow:hidden;border:2px solid var(--headerColor);border-radius:50%;}\n      ion-icon{position:absolute;bottom:0;right:0;background:#fff;color:var(--headerColor);\n        border-radius:50%;border:1px solid var(--headerColor);padding:3px;font-size:13px;\n      }\n    }\n    .likeMoreDiv{text-align:center;border:1px solid var(--headerColor);width:30px;height:30px;\n      border-radius:50%;margin:10px auto;padding:2px 0px;\n      ion-icon{font-size:3px;color:var(--headerColor);background:var(--headerColor);margin:0px 2px;border-radius:50%;}\n    }\n  }\n}\n// Like & Comment Css End\n// Like & Comment Css End\n\n.smallText{p{margin:0px;}}\n\n\napp-image-view.quickAccess{ \n  img{width:50px;height:50px;object-fit:contain;}\n}\napp-image-view.sliderFullDiv{ \n  img{width:100%;height:100%;object-fit:contain;z-index:1;}\n}\napp-image-view.cheerMsgCard{ \n  img{width:100%;height:100%;object-fit:contain;z-index:1;box-shadow:0